<script setup>
import { ref, onMounted } from "vue";
import router from "@/router";
    const descp = ref("")
    const kw = ref("")
    const ad_headline = ref("")
    const isData = ref(false)
    const overlay = ref(false)
    const color = ref("")
    const text = ref("")
    const snackbar = ref(false)

  async function generateIntro(){
    if (descp.value && kw.value !== '') {
      overlay.value = true
      await fetch('https://asciii-backend.herokuapp.com/textblob/content', {
      method: 'POST',
      body: JSON.stringify({
        topic:descp.value,
        kw: kw.value,
        user_id: localStorage.getItem('u_id')
      }),
      headers: {
        'Content-type': 'application/json; charset=UTF-8'
      }
    }).then(function (response) {
      if (response.ok) {
        return response.json();
      }
      return Promise.reject(response);
    }).then(function (data) {
        console.log(data)
      ad_headline.value = data.data
      overlay.value = false
    }).catch(function (error) {
      console.warn('Something went wrong.', error);
    });
  } else {
    snackbar.value = true
    color.value = 'red'
    text.value = 'Please fill all the fields.'
  }
    

}
</script>

<template>
  <div class="main">
      <v-container>
          <v-row>
              <v-col class="right" cols="6">
                <v-banner lines="two">
                    <template v-slot:prepend>
                    <v-avatar><v-icon icon="mdi-file-document-outline" size="x-large"></v-icon></v-avatar>
                    </template>
                    <v-banner-text>
                        <h3>Text Blob Generator</h3>
                        <p>Generates a complete paragraph for any topic.</p>
                    </v-banner-text>
                </v-banner>
                <v-row class="first-row">
                    <v-col cols="1"></v-col>
                    <v-col cols="10">
                        <v-textarea v-model="descp" label="Description" variant="outlined" placeholder="What is your text about?" rows="2"></v-textarea>
                    </v-col>
                    <v-col cols="1"></v-col>
                </v-row>
                <v-row>
                    <v-col cols="1"></v-col>
                    <v-col cols="10">
                        <v-text-field label="Keywords" v-model="kw"  variant="outlined" density="compact"></v-text-field>
                    </v-col>
                    <v-col cols="1"></v-col>
                </v-row>
                <v-row>
                    <v-col cols="4"></v-col>
                    <v-col cols="4">
                        <v-btn color="primary" variant="tonal" @click="generateIntro()">Generate</v-btn>
                    </v-col>
                    <v-col cols="4"></v-col>
                </v-row>
              </v-col>
              <v-col cols="6">
                <v-banner lines="one" text="Output">
                </v-banner>
                <div class="inputArea">
                  <p class="output">{{ad_headline}}</p>
                  <v-progress-circular class="loader" v-show="overlay" color="primary" indeterminate :size="53"></v-progress-circular>
                </div>
                <v-overlay v-model="overlay"></v-overlay>
              </v-col>
          </v-row>
      </v-container>
  </div>
  <div class="text-center ma-2">
    <v-snackbar :timeout="2000" v-model="snackbar" :color="color" elevation="24">
      {{ text }}
    </v-snackbar>
    </div>
</template>

